import { useState, useEffect, useRef } from "react";
import KeyCode from "rc-util/lib/KeyCode";
import { addGlobalMouseDownEvent, getTargetFromEvent } from "../utils/uiUtil";
export default function usePickerInput({
  open,
  value,
  isClickOutside,
  triggerOpen,
  forwardKeyDown,
  onKeyDown,
  blurToCancel,
  onSubmit,
  onCancel,
  onFocus,
  onBlur
}) {
  const [typing, setTyping] = useState(false);
  const [focused, setFocused] = useState(false);
  const preventBlurRef = useRef(false);
  const valueChangedRef = useRef(false);
  const preventDefaultRef = useRef(false);
  const inputProps = {
    onMouseDown: () => {
      setTyping(true);
      triggerOpen(true);
    },
    onKeyDown: (e) => {
      const preventDefault = () => {
        preventDefaultRef.current = true;
      };
      onKeyDown(e, preventDefault);
      if (preventDefaultRef.current)
        return;
      switch (e.which) {
        case KeyCode.ENTER: {
          if (!open) {
            triggerOpen(true);
          } else if (onSubmit() !== false) {
            setTyping(true);
          }
          e.preventDefault();
          return;
        }
        case KeyCode.TAB: {
          if (typing && open && !e.shiftKey) {
            setTyping(false);
            e.preventDefault();
          } else if (!typing && open) {
            if (!forwardKeyDown(e) && e.shiftKey) {
              setTyping(true);
              e.preventDefault();
            }
          }
          return;
        }
        case KeyCode.ESC: {
          setTyping(true);
          onCancel();
          return;
        }
      }
      if (!open && ![KeyCode.SHIFT].includes(e.which)) {
        triggerOpen(true);
      } else if (!typing) {
        forwardKeyDown(e);
      }
    },
    onFocus: (e) => {
      setTyping(true);
      setFocused(true);
      if (onFocus) {
        onFocus(e);
      }
    },
    onBlur: (e) => {
      if (preventBlurRef.current || !isClickOutside(document.activeElement)) {
        preventBlurRef.current = false;
        return;
      }
      if (blurToCancel) {
        setTimeout(() => {
          let { activeElement } = document;
          while (activeElement && activeElement.shadowRoot) {
            activeElement = activeElement.shadowRoot.activeElement;
          }
          if (isClickOutside(activeElement)) {
            onCancel();
          }
        }, 0);
      } else if (open) {
        triggerOpen(false);
        if (valueChangedRef.current) {
          onSubmit();
        }
      }
      setFocused(false);
      if (onBlur) {
        onBlur(e);
      }
    }
  };
  useEffect(() => {
    valueChangedRef.current = false;
  }, [open]);
  useEffect(() => {
    valueChangedRef.current = true;
  }, [value]);
  useEffect(() => addGlobalMouseDownEvent((e) => {
    const target = getTargetFromEvent(e);
    if (open) {
      const clickedOutside = isClickOutside(target);
      if (!clickedOutside) {
        preventBlurRef.current = true;
        requestAnimationFrame(() => {
          preventBlurRef.current = false;
        });
      } else if (!focused || clickedOutside) {
        triggerOpen(false);
      }
    }
  }));
  return [inputProps, { focused, typing }];
}
